:root {
  --textColor: #000;
  --yellow: #ebd96b;
}

body {
  font-family: 'Poppins', sans-serif;
  color: var(--textColor);
}

.container {
  max-width: 1780px;
  margin: 0 auto;
  padding: 0 30px;
}

.title-2 {
  display: inline-block;
  position: relative;
  font-weight: 900;
  font-size: 48px;
  line-height: calc(50 / 48);
  letter-spacing: 0.06em;
}

.title-2::after {
  content: '';

  position: absolute;
  z-index: -1;
  right: 0px;
  bottom: 0px;
  width: 190px;
  height: 32px;

  background-image: url('./../img/icons/decor-yellow.svg');
  background-repeat: no-repeat;
}
