.promo {
  margin-bottom: 63px;
}

.promo__content {
  display: flex;
  justify-content: space-between;
  column-gap: 24px;
  padding-top: 68px;
  padding-left: 120px;

  border-radius: 59px;
  background-color: #f4f6f5;
}

.promo__title {
  max-width: 480px;
  margin-bottom: 40px;
  font-weight: 900;
  font-size: 96px;
  line-height: calc(120 / 96);
}

.promo__desc {
  margin-bottom: 30px;

  font-weight: 400;
  font-size: 32px;
  line-height: calc(48 / 32);
  letter-spacing: -0.05em;
}

.promo__btn-wrapper {
  display: flex;
  column-gap: 34px;
  align-items: center;
}

.promo__btn-wrapper::before {
  content: '';
  display: inline-block;
  width: 190px;
  height: 32px;
  background-image: url('./../../img/icons/decor-yellow.svg');
}

.promo__btn {
  padding: 18px 40px;
  background: #000;
  border-radius: 10px;

  font-weight: 500;
  font-size: 30px;
  line-height: 1.5;
  color: #fff;
}

.highlight {
  position: relative;
  display: inline-block;
}

.highlight span {
  position: relative;
  z-index: 1;
}

.highlight::before {
  content: '';
  display: block;
  position: absolute;
  top: -12px;
  bottom: 0px;
  left: -40px;
  right: -160px;
  background-color: #fff;
  transform: rotate(-2deg);
}

.highlight__yellow::before {
  background-color: var(--yellow);
  right: -110px;
}

.highlight__banner::before {
  right: -30px;
}
